$breakpoints: (
  phone: 480px,
  tablet: 768px,
  big-tablet: 1024px,
  desktop: 1440px,
);

.wrap{
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 10%;
    padding: 100px 0 160px;
    box-shadow: 0px 0px 20px 10px var(--grey-sbrikki);
    text-transform: uppercase;

    @media screen and (min-width: 768px) {
        flex-direction: row;
        padding: 160px 0 160px;
    }
}

.sbrikki{
    font-size: 1.7rem;
    text-align: center;
    padding: 20px 20px 40px;
    width: -webkit-fill-available;
    @media screen and (min-width: 768px) {
        font-size: 2rem;
        padding: 20px;
    }
}

.container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.word{
    letter-spacing: 5px;
    font-size: 2rem;
    position: absolute;
    animation: word 15s infinite ease-in-out;
    @media screen and (min-width: 768px) {
        font-size: 3rem;
    }
}

.word:nth-child(1){
    animation-delay: -15s;
}
.word:nth-child(2){
    animation-delay: -11s;
}
.word:nth-child(3){
    animation-delay: -7s;
}
.word:nth-child(4){
    animation-delay: -3s;
}

@keyframes word{
    0%, 5%, 100%{
        filter: blur(0px);
        opacity: 1;
    }
    20%, 80%{
        filter: blur(35px);
        opacity: 0;
    }
}